body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }
  /* Spinner */
  .spinner {
    display: flex;
    justify-content: center;
    margin-top: 150px;
  }
  
  .Gender-Container {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    position: relative;
  }
  .Gender-Container img {
    height: 220px;
    width: 270px;
    filter: contrast(180%);
    box-shadow: 0 0 21px 5px rgb(185, 184, 184);
  }
  .Gender-Container img:hover {
    box-shadow: 0 0 21px 2px rgb(95, 94, 94);
  }
  .Gender-Container legend {
    position: absolute;
    top: 100px;
    background-color: aliceblue;
    padding: 3px;
    border-radius: 5px;
    cursor: pointer;
  }
  .men-legend {
    position: absolute;
    /* left: 23%; */
    top: 50%;
    text-decoration: none;
    color: black;
    background-color: aliceblue;
    padding: 5px;
    border-radius: 5px;
  }
  .women-legend {
    position: absolute;
    /* left: 49%; */
    top: 50%;
    text-decoration: none;
    color: black;
    background-color: aliceblue;
    padding: 5px;
    border-radius: 5px;
  }
  .access-legend {
    position: absolute;
    margin-left: 25px;
    /* left: 72%; */
    top: 50%;
    text-decoration: none;
    color: black;
    background-color: aliceblue;
    padding: 5px;
    border-radius: 5px;
  }
  
  .gender-inner-container {
    display: flex;
    justify-content: center;
    margin-left: 8px;
    position: relative;
  }